<template>
  <div class="col-md-6 col-lg-4">
    <div class="card mb-4">
      <img class="card-img-top" :src="restaurant.image" alt="Card image cap" width="286px" height="180px" />
      <div class="card-body">
        <p class="card-text title-wrap">
          <router-link :to="{ name: 'RestsDetail', params: { id: restaurant.id } }">{{ restaurant.name }}</router-link>
        </p>
        <span class="badge badge-secondary">{{
          restaurant.Category.name
          }}</span>
        <p class="card-text text-truncate">
          {{ restaurant.description }}
        </p>
      </div>
      <div class="card-footer">
        <button :disabled="isProcessing" v-if="restaurant.isFavorited" type="button"
          class="btn btn-danger btn-border favorite mr-2" @click.stop.prevent="deletedFavorite(restaurant.id)">
          移除最愛
        </button>
        <button :disabled="isProcessing" v-else type="button" class="btn btn-primary btn-border favorite mr-2"
          @click.stop.prevent="addFavorite(restaurant.id)">
          加到最愛
        </button>
        <button :disabled="isProcessing" v-if=" restaurant.isLiked" type=" button" class="btn btn-danger like mr-2"
          @click.stop.prevent="deleteLike(restaurant.id)">
          Unlike
        </button>
        <button :disabled="isProcessing" v-else type="button" class="btn btn-primary like mr-2"
          @click.stop.prevent="addLike(restaurant.id)">
          Like
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import usersAPI from './../apis/users'
import { Toast } from './../utils/helpers'

export default{
  props:{
    initialRestaurant:{
      type:Object,
      required:true
    }
  },
  data(){
    return{
      restaurant:this.initialRestaurant,
      isProcessing:false
    }
  },
  methods:{
    async addFavorite(restaurantId) {
      try {
        this.isProcessing=true
        const { data } = await usersAPI.addFavorite({ restaurantId })
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.restaurant = {
          ...this.restaurant,
          isFavorited: true
        }
        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳加入最愛，請稍後再試'
        })
        console.log('error', error)
      }
    },
    async deletedFavorite(restaurantId){
      try{
        this.isProcessing = true
        const { data } = await usersAPI.deleteFavorite({ restaurantId })
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.restaurant = {
          ...this.restaurant,
          isFavorited: false
        }
        this.isProcessing = false
      }catch(error){
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳移除最愛，請稍後再試'
        })
        console.log('error', error)
      }
    },
    async addLike(restaurantId) {
      try {
        this.isProcessing = true
        const { data } = await usersAPI.addLike({ restaurantId })
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.restaurant = {
          ...this.restaurant,
          isLiked: true
        }
        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳加入，請稍後再試'
        })
        console.log('error', error)
      }
    },
    async deleteLike(restaurantId) {
      try {
        this.isProcessing = true
        const { data } = await usersAPI.deleteLike({ restaurantId })
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.restaurant = {
          ...this.restaurant,
          isLiked: false
        }
        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳移除，請稍後再試'
        })
        console.log('error', error)
      }
    },
  }
}
</script>
